* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 40px;
}
input#search-box:hover,
input#search-box:focus,
input#search-box:active {
  border-color: darkviolet;
}

.country-list {
  list-style: none;
}
.item {
  display: flex;
  align-items: center;
  justify-content: start;
}
.country-name {
  font-size: 24px;
  font-weight: 700;
  margin-left: 15px;
}
.country-wrapper {
  display: flex;
  align-items: center;
  justify-content: start;
}
.country-name-separate {
  font-size: 40px;
  font-weight: 700;
  margin-left: 15px;
}
.country-separate {
  font-size: 24px;
}
.country-separate.r {
  font-size: 24px;
  margin-left: 15px;
}
.country-info {
  margin-left: 40px;
}
